.header {
    border-bottom: 1px solid #ececec;
    @include scr(mobile-only) {
        padding-top: 16px;
        padding-bottom: 16px;
      }
    }
  
  .header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    @include scr(tablet) {
      
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  }
  
  .nav-list__item {
    @include scr(tablet) {
      padding-top: 32px;
      padding-bottom: 32px;
    }
  }
  
  .nav-list,
  .contacts-list {
    display: none;
  
    @include scr(desktop) {
      margin-left: 93px;
    }
  }
  
  .nav-list{
    @include scr(tablet) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  
  .contacts-list {
  
    @include scr(tablet-only) {
      display: block;
  }
  @include scr(desktop) {
    display: flex;
    flex-direction: row;
    align-content: center;
    gap: 10px;
    margin-left: auto;
  }
  }
  
  
  .contacts-list svg {
    @include scr(not-mobile) {
      margin-right: 10px;
      fill: currentColor;
    }
  }
  
  
  
  .nav-list__item+.nav-list__item {
    @include scr(tablet) {
    margin-left: 50px;
  }
  }
  
  
  .nav-list a,
  .contacts-list a {
    @include scr(not-mobile) {
    display: inline-flex;
    justify-content: center;
    align-items: center;
   
    font-family: inherit;
    font-weight: 500;
    line-height: 1.14;
    letter-spacing: 0.02em;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    &:hover,
    &:focus {
      color: var(--accent-color);
    }
  }
  }
  
  .contacts-list__item a {
    @include scr(not-mobile) {
    color: var(--second-text-color);
  }
  @media screen and (max-width: 1999px) {
    font-size: 12px;
  }
  }
  .contacts-list__mail {
    @include scr(desktop) {
  width: 16px;
  height: 12px;
    }
  }
  
  .contacts-list__tel {
    @include scr(desktop) {
      width: 10px;
      height: 16px;
    }
  }
  
  .contacts-list__item+.contacts-list__item {
    @include scr(tablet-only) {
      margin-top: 10px;
    }
    @include scr(desktop) {
    margin-left: 30px;
  }
  }
  
  .nav-list__item {
    position: relative;
  }
  
  .nav-list__item--current {
  
    @include scr(tablet) {
    color: var(--accent-color);
    &::after {
      position: absolute;
      content: "";
      width: 100%;
      height: 4px;
      border-radius: 2px;
      bottom: -1px;
      background-color: var(--accent-color);
    }
  }
  }